@import "@/variable.less";

    #mkt-aplus.pc section[class*='mkt-standard-single-side-image'] {
      display: flex;
      column-gap: 24px;
    }
#mkt-aplus.pc section.reverse[class*='mkt-standard-single-side-image'] {
        flex-direction: row-reverse;
      }
#mkt-aplus.pc section[class*='mkt-standard-single-side-image'] .image {
        width: 40%;
      }
#mkt-aplus.pc section[class*='mkt-standard-single-side-image'] .image-text-container {
        flex: 1;
      }
#mkt-aplus.pc section[class*='mkt-standard-single-side-image'] .image img {
        width: 100%;
      }
#mkt-aplus.mobile .image img {
      width: 100%;
    }
