@import "@/variable.less";

      #mkt-aplus section[class='mkt-standard-image-sidebar'] .main-image img {
        width: 100%;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .main-image p {
        margin: 0px;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .content h4 {
        color: #ccc;
        font-style: italic;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .content .content-desc .mkt-standard-text-block {
          margin-bottom: 16px;
        }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .content .content-bullet-point {
        display: none;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .content .content-bullet-point:has(li) {
        display: block;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .sidebar img {
        width: 100%;
      }
#mkt-aplus section[class='mkt-standard-image-sidebar'] .sidebar .sidebar-desc .mkt-standard-text-block {
          margin-bottom: 12px;
        }
#mkt-aplus.mobile section[class='mkt-standard-image-sidebar'] .content .content-bullet-point {
          padding: 12px 24px;
          background-color: #f0f2f2;
          border-radius: 4px;
        }
#mkt-aplus.mobile section[class='mkt-standard-image-sidebar'] .sidebar {
        box-sizing: border-box;
      }
#mkt-aplus.mobile section[class='mkt-standard-image-sidebar'] .sidebar .sidebar-bullet-point {
          padding: 12px 24px;
          background-color: #f0f2f2;
          border-radius: 4px;
        }
#mkt-aplus.pc section[class='mkt-standard-image-sidebar'] {
      display: grid;
      grid-template-columns: 300px 1fr 250px;
      min-height: 450px;
      grid-column-gap: 12px;
      column-gap: 12px;
    }
#mkt-aplus.pc section[class='mkt-standard-image-sidebar'] .content {
        padding-right: 12px;
      }
#mkt-aplus.pc section[class='mkt-standard-image-sidebar'] .content .content-bullet-point {
          padding: 12px 24px;
          background-color: #f3f3f3;
          border: 1px solid #ddd;
          border-radius: 4px;
        }
#mkt-aplus.pc section[class='mkt-standard-image-sidebar'] .sidebar {
        padding-left: 24px;
        border-left: 1px solid #ccc;
        box-sizing: border-box;
      }
#mkt-aplus.pc section[class='mkt-standard-image-sidebar'] .sidebar .sidebar-bullet-point {
          padding: 12px 24px;
          background-color: #f3f3f3;
          border: 1px solid #ddd;
          border-radius: 4px;
        }
