@import "@/variable.less";

  #mkt-aplus section[class='mkt-standard-comparison-table'] {
    max-width: 100%;
    overflow-x: auto;
  }
#mkt-aplus section[class='mkt-standard-comparison-table'] table {
      display: block;
      border-spacing: 0;
      border-collapse: collapse;
    }
#mkt-aplus section[class='mkt-standard-comparison-table'] table .fixed-column {
        position: -webkit-sticky;
        position: sticky;
        left: 0;
      }
#mkt-aplus section[class='mkt-standard-comparison-table'] table .holder {
        background-color: #fff;
      }
#mkt-aplus section[class='mkt-standard-comparison-table'] table tr {
        display: flex;
      }
#mkt-aplus section[class='mkt-standard-comparison-table'] table tr td {
          display: flex;
          align-items: center;
          flex: 1;
          min-width: 120px;
          padding: 4px 6px;
          word-break: break-all;
        }
#mkt-aplus section[class='mkt-standard-comparison-table'] table tr td img {
            width: 100%;
          }
#mkt-aplus section[class='mkt-standard-comparison-table'] table .metrics-row {
        border-top: 1px solid #ddd;
      }
#mkt-aplus section[class='mkt-standard-comparison-table'] table .metrics-row td:nth-of-type(1) {
          background-color: #f7f7f7;
          border-right: 1px solid #ddd;
        }
#mkt-aplus section[class='mkt-standard-comparison-table'] table .metrics-row td.highlight {
          background-color: #fff5ec;
        }
