@import "@/variable.less";

    #mkt-aplus.pc section[class='mkt-standard-four-image-text-quadrant'] {
      display: grid;
      grid-column-gap: 24px;
      column-gap: 24px;
      grid-template-columns: repeat(2, 1fr);
      grid-auto-rows: -webkit-max-content;
      grid-auto-rows: max-content;
    }
#mkt-aplus.pc section[class='mkt-standard-four-image-text-quadrant'] > div img {
          width: 100%;
        }
#mkt-aplus.mobile section[class='mkt-standard-four-image-text-quadrant'] > div img {
        width: 100%;
      }
