@import "@/variable.less";
#mkt-aplus {
  margin: 0 auto;
  overflow-x: hidden;
  visibility: hidden;
}
#mkt-aplus h3,
  #mkt-aplus h4 {
    margin-top: 0px;
    margin-bottom: 0px;
  }
#mkt-aplus h3 *:empty, #mkt-aplus h4 *:empty {
      display: none;
    }
#mkt-aplus section {
    margin-bottom: 12px;
  }
#mkt-aplus section .text_underline {
      -webkit-text-decoration: underline;
      text-decoration: underline;
    }
#mkt-aplus.pc {
    visibility: inherit;
  }
#mkt-aplus.mobile {
    visibility: inherit;
  }
