@import "@/variable.less";

    #mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] {
      display: grid;
    }
#mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] .main-image img {
          width: 100%;
        }
#mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] .body h3 {
          word-break: break-all;
        }
#mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] .body .block h3 {
            font-size: 22px;
          }
#mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] .tech-specs > div .list {
          margin-top: 12px;
          padding: 12px 24px;
          background-color: #f0f2f2;
          border-radius: 4px;
        }
#mkt-aplus.mobile section[class='mkt-standard-single-image-highlights'] .tech-specs > div .list .mkt-standard-text-block {
            margin-top: 0;
            margin-bottom: 12px;
            display: list-item;
          }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] {
      display: grid;
      grid-template-columns: 300px 1fr 250px;
      grid-column-gap: 24px;
      column-gap: 24px;
    }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .main-image img {
          width: 100%;
        }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .body h3 {
          word-break: break-all;
        }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .body .block h3 {
            font-size: 22px;
          }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .tech-specs > div {
        padding: 0 24px;
        border-left: 1px solid #dddddd;
      }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .tech-specs > div .list {
          margin-top: 12px;
          padding: 12px 24px;
          background-color: #f3f3f3;
          border: 1px solid #ddd;
          border-radius: 4px;
        }
#mkt-aplus.pc section[class='mkt-standard-single-image-highlights'] .tech-specs > div .list .mkt-standard-text-block {
            margin-top: 0;
            display: list-item;
          }
