@import "@/variable.less";

    #mkt-aplus.pc section[class='mkt-standard-image-text-overlay'] {
      position: relative;
    }
#mkt-aplus.pc section[class='mkt-standard-image-text-overlay'] .DARK .image-text-container {
          background-color: rgba(0, 0, 0, 0.5);
          color: #fff;
        }
#mkt-aplus.pc section[class='mkt-standard-image-text-overlay'] .LIGHT .image-text-container {
          background-color: rgba(255, 255, 255, 0.7);
          color: #000;
        }
#mkt-aplus.pc section[class='mkt-standard-image-text-overlay'] .image-text-container {
        position: absolute;
        right: 0;
        top: 50%;
        width: 40%;
        padding: 16px;
        transform: translateY(-50%);
      }
#mkt-aplus.pc section[class='mkt-standard-image-text-overlay'] img {
        width: 100%;
      }
#mkt-aplus.mobile section[class='mkt-standard-image-text-overlay'] img {
        width: 100%;
      }
