@import "@/variable.less";

    #mkt-aplus section[class='mkt-standard-tech-specs'] .tech-specs-item {
      display: flex;
      padding: 8px;
      border-bottom: 1px solid #bbbfbf;
      border-left: 1px solid #bbbfbf;
      border-right: 1px solid #bbbfbf;
    }
#mkt-aplus section[class='mkt-standard-tech-specs'] .tech-specs-item .mkt-standard-text-block {
        margin-bottom: 0;
      }
#mkt-aplus section[class='mkt-standard-tech-specs'] .tech-specs-item > div:nth-of-type(1) {
        flex-basis: 200px;
        display: flex;
        align-items: center;
      }
#mkt-aplus section[class='mkt-standard-tech-specs'] .tech-specs-item > div:nth-of-type(2) {
        flex: 1;
        display: flex;
        align-items: center;
      }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap {
        display: grid;
        grid-column-gap: 24px;
        column-gap: 24px;
      }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.two-columns {
          grid-template-columns: repeat(2, 1fr);
        }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.two-columns .tech-specs-item:nth-of-type(1),
            #mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.two-columns .tech-specs-item:nth-of-type(2) {
              border-top: 1px solid #bbbfbf;
            }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.two-columns .tech-specs-item:nth-of-type(4n - 2),
            #mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.two-columns .tech-specs-item:nth-of-type(4n - 3) {
              background-color: #f0f2f2;
            }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.one-columns .tech-specs-item:nth-of-type(1) {
              border-top: 1px solid #bbbfbf;
            }
#mkt-aplus.pc section[class='mkt-standard-tech-specs'] .tech-specs-wrap.one-columns .tech-specs-item:nth-of-type(2n-1) {
              background-color: #f0f2f2;
            }
#mkt-aplus.mobile section[class='mkt-standard-tech-specs'] .tech-specs-item {
        border-left: 0;
        border-left: initial;
        border-right: 0;
        border-right: initial;
      }
#mkt-aplus.mobile section[class='mkt-standard-tech-specs'] .tech-specs-item:nth-of-type(1) {
          border-top: 1px solid #bbbfbf;
        }
#mkt-aplus.mobile section[class='mkt-standard-tech-specs'] .tech-specs-item > div {
          flex: 1;
        }
