@import "@/variable.less";

      #mkt-aplus.mobile section[class='mkt-standard-multiple-image-text'] .small-image {
        display: none;
      }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .big-container .big-item {
          display: none;
          grid-template-columns: 300px 1fr;
          gap: 12px;
        }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .big-container .big-item.active {
            display: grid;
          }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .big-container .big-item img {
            width: 100%;
          }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .small-image {
        margin-top: 12px;
        display: flex;
        column-gap: 12px;
      }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .small-image .active img {
          box-sizing: border-box;
          opacity: 1;
        }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .small-image > div {
          width: 80px;
          cursor: pointer;
        }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .small-image > div .mkt-standard-text-block {
            text-align: center;
          }
#mkt-aplus.pc section[class='mkt-standard-multiple-image-text'] .small-image > div img {
            width: 100% !important;
            box-sizing: border-box;
            border: 1px solid #888888;
            opacity: 0.3;
          }
#mkt-aplus section[class='mkt-standard-multiple-image-text'] {
  }
